import React from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import Navbar from '../ContentExplorer/Nabar/Navbar';
import ContentExplorer from '../ContentExplorer/ContentExplorer';
import ContentWindow from '../ContentWindow/ContentWindow';

import strApp from '../StrFail/strApp';
import strAppCss from '../StrFail/strAppCss';
import strPackageJson from '../StrFail/strPackageJson';

let strFial = {
	strApp:  strApp,
    strAppCss: strAppCss,
	strPackageJson: strPackageJson
}

const VscContent = (props) => {
	// debugger


    return (
		<>
			<p>Имя стиля: &ensp; {props.id}{`)`} &ensp; <b>{props.nameStyle}</b></p>
			<div className= {`${cc.background} ${cc.decor} ${vsc.appWrapper}`} >
				<Navbar state={props.state} dispatch={props.dispatch} id={props.id}/>
				<div className={vsc.main}>
					<ContentExplorer
						dispatch={props.dispatch}
						state={props.state}
						id={props.id}
					/>
					<ContentWindow
						dispatch={props.dispatch}
						state={props.state}
						strFial={strFial}
						id={props.id}
						style={props.style}
						nameStyle={props.nameStyle}
						dataLength={props.dataLength}
					/>

				</div>
			</div>
		</>
    )
}

export default VscContent;
