import React from 'react';
import ReactDOM from 'react-dom';

import './index.css';
import App from './App';
import store from './redux/store';

console.log("%c  РОССИЯ  ", "background: blue; color: yellow; font-size: x-large; border-left: 5px solid black; border-top: 30px solid white; border-right: 2px solid black; border-bottom: 30px solid red;");



const dispatch = [];
for (let i=0; i < store.getObjectLength(); i++) {
    dispatch.push(store.dispatch[i].bind(store));
}

let renderEntireTree = (state) => {
    ReactDOM.render(
        <React.StrictMode>
            <App
                state={state}
                dispatch = {dispatch}
                dataLength = {store.getObjectLength()}
                // dispatch0={store.dispatch[0].bind(store)}
                // dispatch1={store.dispatch[1].bind(store)}
                // dispatch2={store.dispatch[2].bind(store)}
                // dispatch3={store.dispatch[3].bind(store)}
            />
        </React.StrictMode>,
        document.getElementById('root')
    );
};

renderEntireTree(store.getState());
store.subscribe(renderEntireTree);
