const UPDATE_FILE_APP = 'UPDATE_FILE_APP';
const UPDATE_FILE_APP_CSS = 'UPDATE_FILE_APP_CSS';
const UPDATE_PACKAGE_JSON = 'UPDATE_PACKAGE_JSON';

const SHOW_EXPLORER_STAPE = 'SHOW_EXPLORER__STAPE';

const INITIALIZATION = 'INITIALIZATION';

const reducer = (status, action) => {

    switch (action.type) {
        case UPDATE_FILE_APP:
            status.statusApp = action.flag;
            return status;
        case UPDATE_FILE_APP_CSS:
            status.statusAppCss = action.flag;
            return status;
        case UPDATE_PACKAGE_JSON:
            status.statusPackageJson = action.flag;
            return status;
        case INITIALIZATION:
            status.statusApp = false;
            status.statusAppCss = false;
            status.statusPackageJson = false;
            return status;
        case SHOW_EXPLORER_STAPE:
            status.showExplorer = action.flag;
            return status;
        default:
            return status;
    }
}

export const updateApp = (body) => ({ type:  UPDATE_FILE_APP, flag: body});
export const updateAppCss = (body) => ({ type: UPDATE_FILE_APP_CSS, flag: body});
export const updatePackageJson = (body) => ({ type: UPDATE_PACKAGE_JSON, flag: body});
export const initialization = () => ({ type: INITIALIZATION });
export const showExplorer = (body) => ({ type: SHOW_EXPLORER_STAPE, flag: body});

export default reducer;
