import reducer_item from './reduser';
import * as data from 'react-syntax-highlighter/dist/esm/styles/prism';



let reducer = [],
    state = [],
    dispatch = [];


function objectLength(obj) { //определить количество  модулей
    var result = 0;
    for(var prop in obj) {
      if (obj.hasOwnProperty(prop)) {
        result++;
      }
    }
    return result;
  }
let dataLength = objectLength(data);
// dataLength = 1;

// заполняем reducer
for (let i=0; i <= dataLength; i++) {
    reducer.push(reducer_item);
}

// заполняем state
for (let i=0; i <= dataLength; i++) {
    state.push({
        showExplorer: false,
        statusApp: false,
        statusAppCss: false,
        statusPackageJson: false,
        id: i
    });
}
// console.log(reducer);


// заполняем dispatch
for (let i=0; i <= dataLength; i++) {
    dispatch.push(
        function (action)  {
            this._state.state[i] = reducer[i](this._state.state[i], action);
            this._renderEntireTree(this._state);
        }
    );
}


let store = {
    _state: {
        state
    },
    getState() {
        return this._state;
    },
    _renderEntireTree() {
        console.log('Вызов state');
    },
    subscribe (observer){
        this._renderEntireTree = observer;
    },
    dispatch,
    getObjectLength() {
        return dataLength;
    }
};




window.store = store;

export default store;
