import React from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import { initialization, showExplorer, updateApp, updateAppCss, updatePackageJson } from '../../redux/reduser';
import { APP, APP_CSS, PACKAGE_JSON } from '../../redux/constName';


const Item = (props) => {

    const unwrap = () => {
        // debugger
        props.dispatch(showExplorer(false));
        switch (props.fileName) {
            case APP:
                if (!props.status) {
                    props.dispatch(initialization());
                    props.dispatch(updateApp(true));
                }
            break;
            case  APP_CSS:
                if (!props.status) {
                    props.dispatch(initialization());
                    props.dispatch(updateAppCss(true));
                }
            break;
            case  PACKAGE_JSON:
                if (!props.status) {
                    props.dispatch(initialization());
                    props.dispatch(updatePackageJson(true));
                }
            break;
            default:
                console.log('нет props.fileName');
        }
    }
    return (
        <div className={`${vsc.row__files} ${props.status && `${vsc.backlight}`} ${!props.enabled && `${cc.colorBlocked}`}`} onClick={unwrap}>
            <div className={`${props.ico} ${vsc.offsetExpl} ${cc.colorLightBlue} ${vsc.font14} ${vsc.cursorPointer}`}>
                <code>{props.fileName}</code>
            </div>
        </div>

    )
}

const File = (props) => {
    // console.log(props.state)
    return (
        <>
            {props.fileName === APP &&
                <Item
                    fileName={props.fileName}
                    ico={props.ico}
                    status={props.state.statusApp}
                    enabled={props.enabled}
                    dispatch={props.dispatch}
                />
            }
            {props.fileName === APP_CSS &&
                <Item
                    fileName={props.fileName}
                    ico={props.ico}
                    status={props.state.statusAppCss}
                    enabled={props.enabled}
                    dispatch={props.dispatch}
                />
            }
            {props.fileName === PACKAGE_JSON &&
                <Item
                    fileName={props.fileName}
                    ico={props.ico}
                    status={props.state.statusPackageJson}
                    enabled={props.enabled}
                    dispatch={props.dispatch}
                />
            }

            {/* {props.fileName === APP &&

                <div className={`${vsc.row__files} ${props.state.statusApp && `${vsc.backlight}`} ${!props.enabled && `${cc.colorBlocked}`}`} onClick={unwrap}>
                    <Item props={props}/>
                </div>
            }
            {props.fileName === APP_CSS &&
                <div className={`${vsc.row__files} ${props.state.statusAppCss && `${vsc.backlight}`} ${!props.enabled && `${cc.colorBlocked}`}`} onClick={unwrap}>
                    <Item props={props}/>
                </div>
            } */}


        </>
    )
}

export default File;
