import React, { useEffect, useState } from 'react';
import cc from '../../ColorCode.module.css';
import vsc from '../../ProtoVsc.module.css';
import {APP_CSS, APP, SRC, PACKAGE_JSON} from '../../redux/constName'
import Folder from './Folder';
import File from './File';

const ContentExplorer = (props) => {

    let [status, setStatus] = useState(props.state)
    // debugger
    let className = vsc.explorerHide;

    const unwrap = () => { //для анимации выезда слева на малом экране
        if(status.showExplorer){
            className = vsc.explorerShow;
        } else {
            className = vsc.explorerHide;
        }
    }
    unwrap()

    useEffect( ()  => {
        setStatus(props.state);
    }, [props.state])

    return (
        <div className= {`${vsc.explorer} ${cc.colorWhite} ${className}`}>
        <div className={`${vsc.explorer__header} ${vsc.colorFileName} ${vsc.font14}`}><code>explorer</code></div>
            <div className={vsc.container}>
                <div className={vsc.explorer__column}>
                    <div className={vsc.row__sub} >
                            <Folder folderName={SRC} dispatch={props.dispatch} enabled={true} id={props.id} isFirstDownload={false}>
                                <File fileName={APP_CSS} ico={vsc.fileCss} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                                <File fileName={APP} ico={vsc.fileTS} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                            </Folder>
                            <File fileName={PACKAGE_JSON} ico={vsc.fileNPM} dispatch={props.dispatch} state={props.state} enabled={true} id={props.id}/>
                    </div>
                </div>
            </div>
        </div>
    )

}

export default ContentExplorer;
