import React, { useEffect, useState } from 'react';
import vsc from '../../../ProtoVsc.module.css';
import { showExplorer } from '../../../redux/reduser';



const Navbar = (props) => {
	let [active, setActive] = useState(props.state.showExplorer);
	let [firstDownload, setFirstDownload] = useState(true);
	// debugger
	const click = () => {

        if(active) {
            setActive(false);
			unwrap(false);
			// console.log(1);
        } else {
			setActive(true);
			unwrap(true);
			// console.log(2);
			setFirstDownload(false);
		}
    }

	const unwrap = (bool) => {
		// debugger
		props.dispatch(showExplorer(bool));
    }

    useEffect(
		() => {
		  setActive(props.state.showExplorer);
		},[props.state.showExplorer]
	  );
	return (
		<>
			{/* чтобы не показывалась анимация при перезагрузки страницы */}
			{!active && firstDownload &&
				<div className={vsc.vabar} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}
			{active && !firstDownload &&
				<div className={vsc.vabar} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger} ${vsc.active}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}
			{!active && !firstDownload &&
				<div className={vsc.header} onClick={click}>
					<button type="button" className={`${vsc.navbarToggle} ${vsc.rsMenuForm__burger} ${vsc.notActive}`} >
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
						<span className={vsc.iconBar}></span>
					</button>
				</div>
			}

		</>


	)
}

export default Navbar;
